.practicum-video {
    display: grid;
    grid-template-areas: "player""other-videos";
    grid-template-rows: 0fr 1fr;
    padding-bottom: 50px;
}

.practicum-video .player {
    display: flex;
    width: 100%;
    justify-content: center;
}

.practicum-video .other-videos {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
    row-gap: 20px;
    padding: 20px;
}

.practicum-video .other-videos .other-video-card {
    display: flex;
    color: #222EA2;
    border-radius: 0.8em;
    background-color: white;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    min-height: 75px;
    font-size: 20px;
}

.practicum-video .other-videos .other-video-card .icon {
    flex-grow: 1;
    display: flex;
    align-items: center;
    padding-left: 20px;
    font-size: 36px;
}

.practicum-video .other-videos .other-video-card .video-name {
    flex-grow: 5;
    display: flex;
    align-items: center;
    font-weight: bold;
}


